.ui.header.verification-year{
    border-bottom: 1px solid black;
    margin-left: 60px;
    font-size: 26px;
}
.ui.header.verification-month{
    margin-left: 60px;
    color:grey;
    text-transform: uppercase;
    font-size: 12px;
}
.line{
    border-left:3px solid black;
}
.verification-verifier{   
    display: inline-flex;
    padding: 10px;
    border-radius: .28571429rem;
    background-color: #F6318B1A;
    width: 100%;
    margin-bottom: 10px;
    font-size: 13px;
    
    .verifier{
        display:inline-flex;
        flex: 2;

        .picture{
            width: 55px;
            height: 55px;
            margin-right: 10px;
            border-radius: .28571429rem;
        }
        .title{
            flex-direction: column;
            display:flex;
            color: #F6318B;
            
            .name{
                vertical-align: top;
                font-weight: bold;
            }
            .role{
                opacity: 0.5;
            }
        }

    }
    .description {
       flex: 2;
       color: #F6318B;
       opacity: 0.5;
    }
    .date{
        flex: 1;
        margin-right:10px;
        display: inline-flex;
        flex-direction: column;
        color: #F6318B;
        opacity: 0.5;

        .day{
            vertical-align: top;
            font-weight: bold;
        }
        .time{
            vertical-align: bottom;
            opacity: 0.5;
        }
    }
    

}
#verified{
    background-color: #41CCAA26;

    .title, .description, .date {
        color: #3B5570;
    }
    
}
.verification-status-container {
    max-width: 75%;
    margin-left: auto !important;
    margin-right: auto !important;
    box-shadow: 0px 10px 44px #00000013 !important;
    border: 0 !important;

    > .segment:first-child{
        cursor: pointer;
    }
}