
#navbar {
    border: 0;
    border-radius: 0;
    height: 90px;
    margin: 0;
    background: #F5F7FC 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #0000001A;
    margin-left: 234px;
}
#navbar-title{
    padding-left: 45px;
    text-align: left;
    border: 0;
    font-family: "Manrope Medium";
    font-size: 33px;
    font-weight: bold;
    letter-spacing: 0.48px;
    color: #02053B;
}
#navbar-title:before{
    background: none;
}
#navbar-party:before{
    background: none;
}
#navbar-account:before{
    background: none;
}