.page {
    margin-left: 234px;
}
.page-header{
    display: flex;
    background: #FAFAFA91 0% 0% no-repeat padding-box;
    border-bottom: 1px solid rgb(212, 212, 213);
    height: 60px;
    h1{
        font-family: "Manrope Medium";
        font-size: 14px;
        letter-spacing: 1.4px;
        color: #02053B;
        text-transform: uppercase;
        margin: 0;
        line-height: 60px;
    }
    i{
        line-height: 60px;
        margin-left: 45px;
    }
    a{
        display:flex;
        color: #02053B;
    }
}
.page-content{
    padding: 45px;
    padding-top: 32px;
}
#login-page {
    background: url("../images/rain.png") no-repeat;
    background-size: cover;
    height: 100%;
    margin: 0;

    #logo {
        width: 157px;
        height: 72px;
        margin-bottom: 40px;
    }
}
#root, .app {
    height:100%;
}