#table-footer{
    background: none;
    border: 0;
    box-shadow: none;
    .item:before{
        background: none;
    }
    .item{
        span {
            padding-bottom: 5px;
            margin-top:5px;
            border-bottom: 1px solid transparent;
        }
    }
    .item:hover:not(.disabled){
        background: none;
        span {
            border-bottom: 1px solid black;
            padding-bottom: 5px;
        }
    }
   .ui.table foot th {
       background: none;
   }
   .ui.table thead th {
       background: none;
   }
}
#table-footer-right{
    background: none;
    border: 0;
    box-shadow: none;

    .item:before{
        background: none;
    }
    .item  {
            padding:1px;
            color:rgba(40, 40, 40, 0.3) !important;
            span {
                padding-bottom: 5px;
                margin-top:5px;
                border-bottom: 1px solid transparent;
            }
        
    }
    .item:hover:not(.disabled){
        background: none;
        span {
            border-bottom: 1px solid black;
            padding-bottom: 5px;

        color:black; 
        }
    }
}
.split-slider-in-table{
    max-height: 38px;
}

table.ui.basic.table {
    box-shadow: 0px 10px 44px #00000013 !important;

    border: 0 !important;
}