#verification-section {
    box-shadow: none;
    border: 0;
    border-radius: 0;
    margin-left: -24px;
    margin-right: -24px;
    margin-top: -24px;
}
#verification-section-header{
    background: #F8F9FC 0% 0% no-repeat padding-box;
    border-bottom: 1px solid #E4E4E8;
    border-radius: 0;
    box-shadow: none;
    padding: 6px 24px;
}
#verification-section-header h2{
    font-family: "Manrope Medium";
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 0px;
    color: #333333DE;
}
#verification-body {
    padding:24px;

}
.verification-request{
    > .segment:first-child{
        cursor: pointer;
    }
    box-shadow: 10px 10px 44px #00000013 !important;
    border: 0 !important;
}