#profile-tabs{
    background: #FAFAFA91;
    .items{
        padding-left: 45px;
        display: inline-flex;
    }
    .ui.secondary.pointing.menu {
        margin-left: 45px;
        width: 300px;
        height: 60px;
        margin-bottom: 0;
        border-bottom: 2px solid transparent;
        border-top: 2px solid transparent;
    }
    .ui.segment:first {
        padding: 45px;
        padding-top: 32px;
        border-bottom: none;
    }
    .ui.segment[class*="bottom attached"]:last-child {
        padding: 0;
        border-bottom: none;
    }
}