#table-button {
    background: #4B13A3 0% 0% no-repeat padding-box;
    box-shadow: 0px 5px 20px #4B13A350;
    border-radius: 100px;
    color:white;
    padding: 18px;
    border:none;
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
    margin: auto;
    font-size: 18px;
    font-family: "Manrope Medium";
    font-weight: normal;
}
.ui.button.primary {
   background-color: #4B13A3;
   color: #FFFFFF99;
 }
 .ui.button.primary:active, .ui.button.primary:hover, .ui.button.primary:focus {
  background-color: #5215b6;
  color: #FFFFFF99;
}