#period-tabs{
    background: #FAFAFA91;
    .items{
        padding-left: 45px;
        display: inline-flex;
    }
    .ui.secondary.pointing.menu {
        margin-left: 45px;
        width: 300px;
        height: 60px;
        margin-bottom: 0;
        border-bottom: 2px solid transparent;
    }
    .ui.segments, .ui.grid {
        margin: 45px;
        margin-top: 32px;
        border-bottom: none;
    }
    .ui.segment[class*="bottom attached"]:last-child {
        border-bottom: none;
    } 
    .ui.grid > .row > .column:first-child{
        padding-left: 0;
    }
    .ui.grid > .row > .column:last-child {
        padding-right: 0;
    }
    .ui.grid > .row > .column {
        .ui.segments{
            margin: 0;
            padding: 0;
        }
    }
}
.ui.menu.secondary .item {
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 1px;
}    