.sidebar-header{
    padding: 30px;
    width: 230px;

    img {
        height: 60px;
    }
    margin-bottom: 62px;
}
#sidebar {
    border: 0;
    z-index: 1500;
    width: 234px;
    .item{
        text-align: left;
        height: 44px;
        margin-bottom: 60px;
        font-size: 16px;
        font-weight: normal;
        color: white;
        border-bottom: 0;
        text-align: left;
        letter-spacing: 0.32px;
        padding-left: 30px;
        opacity: 0.39;
        
        border-left: 10px solid transparent;
    }
    .item:before{
        background: none;
    }
    .item:hover, .item.selected{
         border-left: 10px solid #fff;
         opacity: 1;
         background: transparent;
    }
    .item.light{
        color:black;
    }
    .item.light:hover, .item.light.selected{
        border-left: 10px solid black;
        opacity: 1;
        background: transparent;
    }
}