.form-body{
    padding: 28px;
}
.split-form-body{    
    display: flex;
}
.country-checkboxes {
    column-width: 350px;
    column-gap: 10px;
    width: 100%;
}
.country-checkboxes > .checkbox {
  break-inside: avoid-column; /* Prevent element from breaking */
  page-break-inside: avoid; /* Prevent element from breaking in Firefox */
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
}
.ui.form .field > label {
    font-weight: bold;
}

div.ui.icon.input{

    box-shadow: 0px 10px 44px #00000013 !important;
    border: 0 !important;
}